import React from "react";
import { Card, Col, Rate, Row, Spin, Typography } from "antd";
import { LoadingOutlined } from "@ant-design/icons";
import { ISearchItem, ISource } from "../types";

interface IArticleProps {
  articles: ISearchItem[] | undefined;
}

const Articles: React.FC<IArticleProps> = ({ articles }) => {
  const sources: ISource[] = require("../sources.json");

  const getSourceBySite = (site: string): ISource | null => {
    for (const source of sources) {
      if (site.includes(source.site)) {
        return source;
      }
    }

    return null;
  };

  return articles ? (
    <Col span={17}>
      <Row gutter={[40, 40]}>
        {articles?.map((article: ISearchItem, index: number) => (
          <Col span={12} key={index}>
            <a href={article.link} target="blank">
              <Card key={index} style={{ borderRadius: 10 }}>
                <Row justify="space-between" style={{ marginBottom: 10 }}>
                  <Typography.Title level={5}>
                    {getSourceBySite(article.link)?.site}
                  </Typography.Title>
                  <Rate
                    allowHalf
                    disabled
                    value={getSourceBySite(article.link)?.rate}
                  />
                </Row>
                <Typography.Text>{article.title}</Typography.Text>
              </Card>
            </a>
          </Col>
        ))}
      </Row>
    </Col>
  ) : (
    <Col span={17}>
      <Row justify="center">
        <Spin
          style={{ marginTop: 250 }}
          indicator={
            <LoadingOutlined style={{ fontSize: 100, color: "#692e90" }} spin />
          }
        />
      </Row>
    </Col>
  );
};

export default Articles;
